<template>
  <div class="main flex">
    <div class="aside">
      <div class="circle" id="char"></div>
      <div class="lamp">
        <div class="item">
          <div class="tit">旋转钻进</div>
          <img src="@/assets/icon/green.png" alt="">
        </div>
        <div class="item">
          <div class="tit">滑动钻进</div>
          <img src="@/assets/icon/green.png" alt="">
        </div>
        <div class="item">
          <div class="tit">起钻</div>
          <img src="@/assets/icon/green.png" alt="">
        </div>
        <div class="item">
          <div class="tit">下钻</div>
          <img src="@/assets/icon/red.png" alt="">
        </div>
        <div class="item">
          <div class="tit">开泵</div>
          <img src="@/assets/icon/green.png" alt="">
        </div>
      </div>
      <div class="bot flex">
        <ul>
          <li>
            <div class="num">124</div>
            <p>井深(M)</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>钻时(min/h)</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>钻头位置(M)</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>机械钻速(m/h)</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>入口密度(g/cm³)</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>出口密码(g/cm³)</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>钻头入井时间</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>钻头进尺(M)</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>泵冲1</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>泵冲2</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>泵冲3</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>泵冲4</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>总池体积(m³)</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>总池体积变化量</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>灌浆罐体积(m³)</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>全烃(%)</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>灌浆罐体积变化量</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>入口流量(L/s)</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>相对流量(%)</p>
          </li>
          <li>
            <div class="num">124</div>
            <p>迟到井深(m)</p>
          </li>
        </ul>
        <div class="img">
          <img src="@/assets/well/bg.png" alt="" class="bg">
          <img src="@/assets/well/pip.png" alt="" class="pip">
        </div>
      </div>
    </div>
    <div class="sec">
      <div class="tabs flex">
        <div class="tab" v-for="(item,i) in tabs" :key="i">
          <div v-if="i == 1" class="rule">标尺</div>
          <div v-else class="item" v-for="(i,j) in item" :key="j">
            <div class="tit">{{i.txt}}</div>
            <div class="flex num">
              <div class="min">{{i.min}}</div>
              <div class="line" :style="{background: i.color }"></div>
              <div class="max">{{i.max}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts'
export default {
  data () {
    return {
      tabs: [
        [
          {
            txt: '钻　　压（kN）',
            min: -350,
            max: 350,
            color: 'red'
          },
          {
            txt: '钩　　载（kN）',
            min: 0,
            max: 3000,
            color: 'red'
          },
          {
            txt: '转　　速（r/min）',
            min: 0,
            max: 150,
            color: 'red'
          },
          {
            txt: '扭　　矩（kN.m）',
            min: 0,
            max: 50,
            color: 'red'
          },
          {
            txt: '大钩高度',
            min: 0,
            max: 50,
            color: 'red'
          },
          {
            txt: '钻头比能',
            min: 0,
            max: 100,
            color: 'red'
          }
        ],
        [],
        [
          {
            txt: '立　　压（MPa）',
            min: 0,
            max: 40,
            color: 'red'
          },
          {
            txt: '泵　　冲1',
            min: 0,
            max: 150,
            color: 'red'
          },
          {
            txt: '泵　　冲2',
            min: 0,
            max: 150,
            color: 'red'
          },
          {
            txt: '泵　　冲3',
            min: 0,
            max: 150,
            color: 'red'
          },
          {
            txt: '排　　量（L/s）',
            min: 0,
            max: 150,
            color: 'red'
          },
          {
            txt: '相对流量（%）',
            min: 0,
            max: 150,
            color: 'red'
          }
        ],
        [
          {
            txt: '全烃含量（MPa）',
            min: 0,
            max: 100,
            color: 'red'
          },
          {
            txt: '钻时（min/m）',
            min: 0,
            max: 60,
            color: 'red'
          },
          {
            txt: '总池体积（m³）',
            min: 0,
            max: 400,
            color: 'red'
          },
          {
            txt: '伽　　马（API）',
            min: 0,
            max: 150,
            color: 'red'
          },
          {
            txt: '出口密度（g/cm³）',
            min: 0,
            max: 150,
            color: 'red'
          },
          {
            txt: '入口密度（g/cm³）',
            min: 0,
            max: 150,
            color: 'red'
          }
        ]
      ]
    }
  },

  components: {},

  computed: {},

  mounted: function() {
    this.draw();
  },

  methods: {
    draw() {
      let char = echarts.init(document.getElementById('char'));
      let param = {
        name: '工程参数',
        type: 'gauge',
        radius: '42%',
        startAngle: 225,
        endAngle: -45,
        clockwise: true,
        min: 0,
        splitNumber: 10,
        axisLine: {
          lineStyle: {
            color: [
              [ 0.6, 'green' ],
              [ 0.8, 'yellow' ],
              [ 1, "red" ]
            ],
            width: 14,
            shadowColor: 'rgba(0, 0, 0, 0.5)',
            shadowBlur: 10,
            opacity: 0.8
          }
        },
        splitLine: {
          show: true,
          length: 14,
          lineStyle: {
            width: 1
          }
        },
        axisTick: {
          lineStyle: {
            color: '#aaa'
          }
        },
        axisLabel: {
          color: 'white',
          fontSize: 8,
          distance: 3
        },
        pointer: {
          width: 4,
          length: '60%'
        },
        title: {
          color: '#fff',
          offsetCenter: [0,'70%'],
          fontSize: 12
        },
        detail: {
          formatter:'{value}',
          offsetCenter: [0,'30%'],
          fontSize: 16
        },
      }
      let option = {
        title: { 
          text: '井号： xxxx' ,
          x:'center',
          textStyle: {
            color: '#fff'
          }
        },
        tooltip : {
          formatter: "{a} <br/>{b} : {c}"
        },
        toolbox: {
          feature: {
            // restore: {},
            saveAsImage: {
              backgroundColor: '#333'
            }
          },
          right: 16,
          bottom: 14
        },
        series: [
          {
            ...param,
            max: 3000,
            center: ['12%','24%'],
            data: [{value: 50, name: '钩载(kN)'}]
          },
          {
            ...param,
            min: -100,
            max: 400,
            center: ['12%','70%'],
            data: [{value: 50, name: '转速(r/min)'}]
          },
          {
            ...param,
            min: -150,
            max: 300,
            axisLine: {
              lineStyle: {
                color: [
                  [ 0.8, 'green' ],
                  [ 0.9, 'yellow' ],
                  [ 1, "red" ]
                ],
                width: 14,
                shadowColor: 'rgba(0, 0, 0, 0.5)',
                shadowBlur: 10,
                opacity: 0.8
              }
            },
            center: ['37%','44%'],
            data: [{value: 50, name: '钻压(kN)'}]
          },
          {
            ...param,
            max: 60,
            axisLine: {
              lineStyle: {
                color: [
                  [ 0.5, 'green' ],
                  [ 0.9, 'yellow' ],
                  [ 1, "red" ]
                ],
                width: 14,
                shadowColor: 'rgba(0, 0, 0, 0.5)',
                shadowBlur: 10,
                opacity: 0.8
              }
            },
            center: ['63%','44%'],
            data: [{value: 50, name: '扭矩(kN.m)'}]
          },
          {
            ...param,
            max: 50,
            center: ['88%','24%'],
            data: [{value: 50, name: '套压(MPa)'}]
          },
          {
            ...param,
            max: 50,
            center: ['88%','70%'],
            data: [{value: 50, name: '立压(MPa)'}]
          }
        ]
      };
      char.setOption(option);
      setInterval(function (){
        option.series[0].data[0].value = parseInt(Math.random()*3000)
        option.series[1].data[0].value = parseInt(Math.random()*400)
        option.series[2].data[0].value = parseInt(Math.random()*300)
        option.series[3].data[0].value = parseInt(Math.random()*60)
        option.series[4].data[0].value = parseInt(Math.random()*50)
        option.series[5].data[0].value = parseInt(Math.random()*50)
        char.setOption(option,true);
      },1000);
    }
  }
}
</script>

<style lang='scss' scoped>
.main{
  background: #27496adc;
  min-width: 1080px;
  width: 96%;
  height: calc(100% - 40px);
  padding: 20px;
  margin: 20px auto;
  box-sizing: border-box;
  border: 1px solid #fff;
  border-radius: 3px;
}
.aside {
  width: 660px;
  background: rgba(0,0,0,.5);
  overflow: auto;
  padding: 10px;
  box-sizing: border-box;
  color: #fff;
  position: relative;
  .circle {
    height: 360px;
  }
  .lamp {
    position: absolute;
    width: 350px;
    height: 90px;
    left: 0;
    right: 0;
    top: 300px;
    margin: auto;
    .item {
      width: 70px;
      text-align: center;
      font-size: 12px;
      float: left;
      img {
        width: 50px;
        height: 50px;
        display: block;
        margin: 10px auto 0;
      }
    }
  }
  .bot {
    margin-top: 40px;
    height: calc(100% - 400px);
    ul {
      width: 400px;
      overflow: hidden;
      li {
        width: 25%;
        float: left;
        text-align: center;
        border: 1px dashed #02C1CF;
        box-sizing: border-box;
        .num {
          color: #fff;
          padding-top: 6px;
          font-size: 18px;
        }
        p {
          font-size: 12px;
          color: #02C1CF;
          margin: 8px 0;
        }
      }
    }
    .img {
      width: 230px;
      position: relative;
      .bg {
        width: 100%;
        height: 100%;
      }
      .pip {
        position: absolute;
        left: 50%;
        top: 10%;
      }
    }
  }
}
.sec {
  width: calc(100% - 670px);
  overflow: auto;
  background: rgba(0,0,0,.5);
  .tab {
    background: #02C1CFaa;
    flex: 1;
    text-align: center;
    .item {
      padding: 10px 16px;
      border: 1px solid rgb(2, 207, 12);
    }
    .num {
      align-items: center;
      font-size: 10px;
      .line {
        flex: 1;
        height: 2px;
        background: greenyellow;
        margin: 0 4px;
      }
    }
  }
}
</style>



